.profile__name {
font-size: 42px;
line-height: 48px;
font-weight: 500;
margin: 0 0 16px 0;
max-width: 294px;
text-overflow: ellipsis;
white-space: nowrap;
overflow: hidden;
}
@media screen and (max-width:1023px) {
  .profile__name {
    max-width: 189px;
    font-size: 27px;
    line-height: 33px;
    text-align: center;
  }
}
